<template>
    <div class="minecraft-server-card bg-gray-200 border border-gray-300" v-if="serverStatus">
        <img :src="serverStatus.favicon ?? placeholderUrl" />
        <div class="server-info">
            <div class="server-desc px-4 py-2">
                <h3 class="text-lg font-bold">
                    {{ serverStatus.motd !== '' ? serverStatus.motd : headerPlaceholder }}
                </h3>
                <p class="text-xs text-gray-500">{{ serverStatus.host }}:{{ serverStatus.port }}</p>
                <div class="py-2 flex flex-row flex-wrap">
                    <span class="border-x border-gray-300 px-2">
                        Version {{ serverStatus.server.name ?? '-' }}
                    </span>

                    <span class="border-x border-gray-300 px-2">
                        Players {{ serverStatus.players.now }}/{{ serverStatus.players.max }}
                    </span>

                    <span class="border-x border-gray-300 px-2" :class="{ 'text-red': !serverStatus.online, 'text-lime': serverStatus.online}">
                        {{ serverStatus.online ? 'Online' : 'Offline' }}
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import minecraftServerStatus from "minecraft-server-status";
import { onBeforeMount, ref, defineProps } from "vue";

const placeholderUrl = 'http://placehold.jp/100x100.png?text=N/A';
const headerPlaceholder = 'Unavailable';

const props = defineProps({
    host: {
        type: String,
        required: true,
    },
    port: {
        type: Number,
        required: false,
        default: 25565,
    },
});

const getStatus = (host, port = 25565) => {
  return new Promise((resolve) => {
    minecraftServerStatus(host, port, res => {
        resolve({host, port, ...res});
    });
  });
};

const serverStatus = ref();

onBeforeMount(async () => {
	serverStatus.value = await getStatus(props.host, props.port);
	console.log(serverStatus.value);
});

</script>

<style scoped>

	.minecraft-server-card {
		height: 100px;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		flex-shrink: 0;
		width: 550px;

		border-radius: 10px;
		/* border: 1px solid gray; */
		overflow: hidden;

		font-family: 'Minecraftia', sans-serif;
	}

	.text-lime {
		color: green;
	}

	.text-red {
		color: red;
	}

	.server-info {
		flex-grow: 1;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.server-status {
		flex-shrink: 0;
	}

	.server-desc {
		flex-grow: 1;
	}

    @font-face {
        font-family: Minecraftia;
        src: url(data:font/truetype;base64,);
    }
</style>